<template>
  <v-container fluid class="d-flex justify-center pa-0 mt-0">
    <v-card tile elevation="0" align="center" v-resize="onResize"
      width="100%" max-width="1500px"> 
      <v-tabs grow >
        <v-tab id="tab1">실종 반려견 보기</v-tab>
        <v-tab :disabled="!login_info.number"
        v-text="!!login_info.number ? '실종 반려견 등록' :
                '실종 반려견 등록(로그인 필요)'">실종 반려견 등록</v-tab>
        <v-tab-item>
          <v-layout class="d-flex justify-center">
            <v-col :cols="cols" class="d-flex">
              <v-select
                v-model="place"
                :items="states"
                label="지역 선택"
                hide-details
                single-line
                class="pa-0 mt-0"
              ></v-select>
              <v-btn icon @click="searchPlace">
                <v-icon>mdi-magnify</v-icon>
              </v-btn>
            </v-col>
          </v-layout>
          <v-divider></v-divider>
          <v-layout wrap class="pa-3" v-if="!ad_brief_list">
            검색 결과가 없습니다.
          </v-layout>
          <v-layout wrap class="pa-3" v-if="ad_brief_list">
              <v-col :cols="cols" v-for="(bar, index) in ad_brief_list" :key="index">
                <a @click="getPoster(index)">
                  <v-card  tile width="344" height="330">
                    <v-img
                      :src="`api/img/get_img/abandoned/${bar.main_img}`"
                      height="200px"
                    ></v-img>
                    <v-divider></v-divider>
                    <v-card-title class="text-left">
                      {{bar.ad_title}}
                    </v-card-title>
                    <v-card-subtitle class="text-left">
                      {{bar.ad_place}}
                    </v-card-subtitle>
                  </v-card>
                </a>
              </v-col>
          </v-layout>
          <v-divider></v-divider>
        </v-tab-item>
        <v-tab-item>
          <InputAD/>
        </v-tab-item>
      </v-tabs>
      <v-dialog  v-model="poster_dialog" tile fullscreen open-delay="300000">
          <v-card tile elevation="0"  align="center" class="pdf_background" style="background-color:gray;">
            <div class="my-4" style="width:auto;">
              <v-card class="my-4" elevation="0" tile width="420px" height="650px" style="overflow:auto; background-color:gray;">
                <Poster
                  ref="poster"
                  :form="form"
                  :ad_honorarium="honorarium"
                  :main_img_url="main_img_url"
                  :sb_img_urls="sb_img_urls"
                  @child="poster_dialog = false"/>
              </v-card>
            </div>
            <v-card-actions color="white" class="d-flex justify-center">
                <v-btn color="#F44336" dark tile elevation="0"
                  @click="pdfDownload" class="ma-1" width="81"
                >
                  PDF
                </v-btn>
                <v-btn class="ma-1" width="81" color="primary"
                  tile elevation="0" @click="poster_dialog = false"
                >
                  close
                </v-btn>
            </v-card-actions>
          </v-card>
      </v-dialog>
    </v-card>
  </v-container>
</template>

<script>
import InputAD from "./AbandonedDog_input"
import Poster from "./Poster"

import { mapGetters } from 'vuex'
const member = 'member';
export default {
  data: () => ({
    tab:null,
    show: false,
    states: ['전국', '서울', '인천', '대전', '광주', '대구', '울산', '부산', '경기', '강원', '세종', '충남', '충북', '전남', '전북', '경남', '경북', '제주'],
    cols:null,
    ad_brief_list:null,
    place:null,
    poster_dialog:null,
    form:null,
    honorarium:null,
    main_img_url:null,
    sb_img_urls:null,
    disb:false,
  }),
  computed:{
    ...mapGetters(member, {
      login_info: 'GET_LOGIN_INFO'
    }),
  },
  components:{
    InputAD,
    Poster,
  },
  methods :{
    //window 사이즈 변경시 poster들의 레이아웃 변경
    onResize(){
      const width = window.innerWidth;
      this.cols = width < 720 ? 12 : width < 1050 ? 6 : width < 1410 ? 4 : 3;
    },
    //기본 검색, 마운트동시에 실행
    getAllPoster(){
      this.$http.get('/api/abandoned/').then((res) => {
        this.changeContent(res.data);
      }).catch(err => {
        alert('실종 반려견 정보를 가져오는 중 문제가 발생했습니다.');
      })
    },
    getPoster(idx){
      this.$http.get('/api/abandoned/target/' + this.ad_brief_list[idx].num).then((res) => {
        if(res.data.message){
          const data = res.data.result[0], sb_imgs = JSON.parse(data.sb_imgs);
          this.form = {
            ad_title : data.ad_title,
            ad_name : data.ad_name,
            ad_date : data.ad_date,
            ad_place : data.ad_place,
            ad_pone : data.ad_pone,
            ad_date : data.ad_date,
            ad_detail : data.ad_detail,
          }
          this.honorarium = data.ad_honorarium ?  {bl:true, hr:data.ad_honorarium} : {bl:false, hr:null};
          let paths = {
            main_img : data.main_img
          }
          Object.keys(sb_imgs).forEach(f => {
            paths[f] = sb_imgs[f];
          })
          this.$http.post('/api/img/rpb', paths).then((res) => {
              const data = res.data.b_imgs;
              this.main_img_url = data.main_img;
              Object.keys(sb_imgs).forEach(f => {
                sb_imgs[f] = data[f];
              });
              this.sb_img_urls = sb_imgs;
              this.poster_dialog = true;
            }).catch(err => {
              alert("포스터를 불러오는 과정에서 문제가 발생했습니다.")
            });
        }
      }).catch(err => {
        console.log(err);
        alert('실종 반려견 정보를 가져오는 중 문제가 발생했습니다.');
      })
    },
    //지역 검색
    searchPlace(){
      const place = this.place;
      console.log(place);
      if(!place || place === '전국' ){
        this.getAllPoster()
        return
      }
      this.$http.get('/api/abandoned/place_search/' + place).then((res) => {
        this.changeContent(res.data);
      }).catch(err => {
        alert('실종 반려견 정보를 가져오는 중 문제가 발생했습니다.');
      })
    },
    //검색 완료후 poster 데이터 변경
    changeContent(data){
      this.ad_brief_list = data.message ? data.result : null;
    },
    //자식 poster의 pdf생성 메소드 호출
    pdfDownload(){
      this.$refs.poster.createPDF();
    }
  },
  mounted(){
    this.onResize();
    this.getAllPoster();
  }, 
  
}
</script>

<style scoped>
.pdf_background{
  overflow-x:auto; 
  background-color:gray;
}
</style>