<template>
    <v-container fluid style="padding:0px; height:100%; background-color:#F8F3F2">
        <div id="member_page">
            <v-card tile  v-if="ext_none">
                <v-list>
                    <v-list-group :value="false" v-for="(bar, i) in abandoneds" :key="i">
                        <template v-slot:activator>
                            <v-list-item-title v-text="bar.ad_name" class="font-weight-bold"></v-list-item-title>
                        </template>
                        <div class="content">
                            <v-layout wrap>
                                <v-col cols="12" style="color:black; overflow:auto;">
                                    <v-simple-table>
                                        <template v-slot:default>
                                            <thead>
                                                <tr>
                                                    <th >
                                                        제목
                                                    </th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <td>{{ bar.ad_title }}</td>
                                                </tr>
                                            </tbody>
                                        </template>
                                    </v-simple-table>
                                    <v-divider></v-divider>
                                    <v-simple-table>
                                        <template v-slot:default>
                                            <thead>
                                                <tr>
                                                    <th >
                                                        이름
                                                    </th>
                                                    <th >
                                                        연락번호
                                                    </th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <td>{{ bar.ad_name }}</td>
                                                    <td>{{ bar.ad_pone }}</td>
                                                </tr>
                                            </tbody>
                                        </template>
                                    </v-simple-table>
                                    <v-divider></v-divider>
                                    <v-simple-table>
                                        <template v-slot:default>
                                            <thead>
                                                <tr>
                                                    <th>
                                                        사례금
                                                    </th>
                                                    <th>
                                                        날짜
                                                    </th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <td v-text="!!bar.ad_honorarium ? bar.ad_honorarium : '없음' "></td>
                                                    <td>{{ bar.ad_date }}</td>
                                                </tr>
                                            </tbody>
                                        </template>
                                    </v-simple-table>
                                    <v-divider></v-divider>
                                    <v-simple-table>
                                        <template v-slot:default>
                                            <thead>
                                                <tr>
                                                    <th>
                                                        장소
                                                    </th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <td>{{ bar.ad_place }}</td>
                                                </tr>
                                            </tbody>
                                        </template>
                                    </v-simple-table>
                                    <v-divider></v-divider>
                                    <v-simple-table>
                                        <template v-slot:default>
                                            <thead>
                                                <tr>
                                                    <th>
                                                        내용
                                                    </th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <td>{{ bar.ad_detail }}</td>
                                                </tr>
                                            </tbody>
                                        </template>
                                    </v-simple-table>
                                </v-col>
                                <v-col cols="12" style="position:relative; text-align:center;">
                                    <v-btn tile elevation="0" dark color="green" @click="openPoster(bar.num)">보기</v-btn>
                                    <v-btn tile elevation="0" dark color="primary" @click="openForm(bar)">수정</v-btn>
                                    <v-btn tile elevation="0" dark color="red" @click="deletePoster(bar, i)">삭제</v-btn>
                                </v-col>
                            </v-layout>
                        </div>
                    </v-list-group>
                </v-list>
            </v-card>
            <h4 v-else class="ext_none">
                활동 정보가 없습니다.
            </h4>
        </div>
        <v-dialog  v-model="poster_dialog" tile fullscreen>
            <v-card tile elevation="0"  align="center" style="background-color:gray; overflow-x:auto;">
                <div class="py-4" style="width:auto;">
                    <v-card tile width="420px" height="650px" style="overflow:auto; background-color:gray;">
                        <Poster
                            ref="poster"
                            :form="form"
                            :ad_honorarium="honorarium"
                            :main_img_url="main_img_url"
                            :sb_img_urls="sb_img_urls"
                            @child="poster_dialog = false"/>
                    </v-card>
                </div>
                <v-card-actions color="white" class="d-flex justify-center">
                    <v-btn color="#F44336" dark tile elevation="0"
                        @click="pdfDownload" class="ma-1" width="81">
                        PDF
                    </v-btn>
                    <v-btn class="ma-1" width="81" color="primary"
                        tile elevation="0" @click="poster_dialog = false">
                        close
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog 
            v-model="update_form" fullscreen
             eager>
            <v-card tile elevation="0" align="center" color="white">
                <v-toolbar dark color="primary">
                    <v-toolbar-title>실종 반려견 수정</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-btn icon dark fab @click="stepToBack">
                        <v-icon>mdi-close</v-icon>
                    </v-btn>
                </v-toolbar>
                <div style="width:90%; max-width:1500px;">
                    <div style="padding:5% 0%;">
                        <UpdateForm ref="update_form"/>
                    </div>
                </div>
            </v-card>
        </v-dialog>
    </v-container>
</template>

<script>
import Poster from "../../components/abandoneddog/Poster";
import UpdateForm from "../../components/abandoneddog/AbandonedDog_input";
import { mapMutations, mapGetters } from 'vuex';
const name = 'member'
export default {
    data: () => ({
        abandoneds : [],
        poster_dialog:null,
        form:null,
        honorarium:null,
        main_img_url:null,
        sb_img_urls:null,
        update_form:false,
        ext_none:null,
    }),
    computed:{
        ...mapGetters(name, {
            login_info: 'GET_LOGIN_INFO'
        }),
    },
    watch:{
        //로그 아웃 감지
        login_info: function(hook){
            !!hook.number || this.$router.push('/');
        },
        //활동 내역 감지
        abandoneds: function(hook){
            this.ext_none = hook.length > 0 ? true : false;
        },
    },
    methods:{
        getAllAbandoned(){
            this.$http.get('/api/member/get_abandoned').then(res => {
                if(res.data.code === 1){
                    this.abandoneds = res.data.item;
                }else if(res.data.code === 2){
                    alert('장시간 동작이 없어 세션이 만료되었습니다. 다시 로그인 해주세요.');
                    this.$router.push('/');
                }else{
                    alert('반려견 정보를 불러오지 못했습니다.');
                    this.$router.push('/')
                }
            }).catch(err => {
                alert('반려견 정보를 불러오지 못했습니다.');
                this.$router.push('/');
            })
        },
        getForm(data){
            const form = {
                ad_title : data.ad_title,
                ad_name : data.ad_name,
                ad_date : data.ad_date,
                ad_place : data.ad_place,
                ad_pone : data.ad_pone,
                ad_date : data.ad_date,
                ad_detail : data.ad_detail,
            };
            return form;
        },
        getHonorarium(data){
            return data ? {bl:true, hr:data} : {bl:false, hr:null};
        },
        getSubImgs(data){
            const sb_imgs = JSON.parse(data);
            Object.keys(sb_imgs).forEach(f => {
                sb_imgs[f] = '/api/img/get_img/abandoned/' + sb_imgs[f];
            })
            return sb_imgs;
        },
        openPoster(num){
            this.$http.get('/api/abandoned/target/' + num).then((res) => {
                if(!!res.data.message){
                    const data = res.data.result[0], sb_imgs = JSON.parse(data.sb_imgs);
                    this.form = {
                    ad_title : data.ad_title,
                    ad_name : data.ad_name,
                    ad_date : data.ad_date,
                    ad_place : data.ad_place,
                    ad_pone : data.ad_pone,
                    ad_date : data.ad_date,
                    ad_detail : data.ad_detail,
                    }
                    this.honorarium = data.ad_honorarium ?  {bl:true, hr:data.ad_honorarium} : {bl:false, hr:null};
                    console.log(data);
                    
                    let paths = {
                    main_img : data.main_img
                    }
                    Object.keys(sb_imgs).forEach(f => {
                    paths[f] = sb_imgs[f];
                    })
                    this.$http.post('/api/img/rpb', paths).then((res) => {
                        const data = res.data.b_imgs;
                        console.log(data);
                        this.main_img_url = data.main_img;
                        Object.keys(sb_imgs).forEach(f => {
                        sb_imgs[f] = data[f];
                        });
                        this.sb_img_urls = sb_imgs;
                        this.poster_dialog = true;
                    }).catch(err => {
                        alert("포스터를 불러오는 과정에서 문제가 발생했습니다.")
                    });
                }
        }).catch(err => {
          console.log(err);
          alert('실종 반려견 정보를 가져오는 중 문제가 발생했습니다.');
        })
        },
        openForm(bar){
            this.update_form = true;
            console.log(this.$refs.update_form);
            const form = this.$refs.update_form,
            sb_imgs = JSON.parse(bar.sb_imgs);
            form.update = {
                num : bar.num,
                main_key : bar.main_img,
                sb_keys : sb_imgs
            };
            console.log(form.update);
            const {getImgBlod, getForm} = this;
            const my_form = getForm(bar);
            form.ad_swich = !!bar.ad_honorarium ?  true : false;
            form.ad_honorarium = bar.ad_honorarium;
            Object.keys(my_form).forEach(f=>{
                form[f] = my_form[f];
            })
            getImgBlod(bar.main_img, form, 'main_img');
            Object.keys(sb_imgs).forEach(f=>{
                getImgBlod(sb_imgs[f], form, f);
            })
            
        },
        getImgBlod(item, target, k){
            let url = '/api/img/get_img/abandoned/' + item;
            const toDataURL = url => fetch(url)
            .then(response => response.blob())
            .then(blob => new Promise((resolve, reject) => {
                const reader = new FileReader()
                reader.onloadend = () => resolve(reader.result)
                reader.onerror = reject
                reader.readAsDataURL(blob)
            }))
            toDataURL(url)
            .then(dataUrl => {
                var fileData = this.dataURLtoFile(dataUrl, item);
                if(!k.indexOf('sb')){
                    const i = Number(k.split('sb')[1]);
                    target.subImgClick(i, true);
                    target.sb_imgs[i] = fileData;
                    target.subImgInput(fileData);
                }else{
                    target.main_img = fileData;
                    target.mainImgInput(fileData);
                }
            })
        },
        dataURLtoFile(dataurl, filename) {
            var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
            bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
            while(n--){
                u8arr[n] = bstr.charCodeAt(n);
            }
            return new File([u8arr], filename, {type:mime});
        },
        stepToBack(){
            this.update_form = false;
            this.$refs.update_form.e1 = 1;
        },
        deletePoster(bar, idx){
            const params = {
                num : bar.num,
                main_key : bar.main_img,
                sb_keys : bar.sb_imgs
            }
            this.$http.post('api/abandoned/delete_poster', params).then(res => {
                if(res.data.code === 1){
                    this.abandoneds.splice(idx, 1);
                }else if(res.data.code === 2){
                    alert('장시간 동작이 없어 세션이 만료되었습니다. 다시 로그인 해주세요.');
                    this.$router.push('/');
                }else{
                    alert('삭제 과정에서 문제가 발생했습니다.');
                }
            }).catch(err => {
                console.log(err);
                alert('삭제 과정에서 문제가 발생했습니다.');
            })
        },
        pdfDownload(){
            this.$refs.poster.createPDF();
        }
    },
    mounted(){
        this.getAllAbandoned();
    },
    components:{
        Poster, UpdateForm
    }
}
</script>

<style scoped>
    @import '../../css/member/abandoned.css';
</style>

